import './mindmap.less';
import React from 'react';
import Node from './Node';
import NodeList from './NodeList';

export default class Mindmap extends React.Component {
    constructor(props) {
        super(props);
    }

    render() {
        var rootNode = this.props.data;

        return (
            <div className="mindmap">
                <Node isRoot={true} data={rootNode} expand={true} />
                <NodeList nodes={rootNode.children} level={1} />
            </div>
        );
    }
}


