import React from 'react';
import Node from './Node';

export default class NodeList extends React.Component {
    constructor(props) {
        super(props);
    }

    render() {
        var nodes = this.props.nodes;

        if (nodes && nodes.length) {
            var level = this.props.level;
            var nodeList = nodes.map(
                (node) =>
                    <li className="children__item" key={node.id}>
                    <Node data={node} expand={true} />
                    <NodeList nodes={node.children} level={level + 1} /> 
                    </li>,
                this
            );

            return (
                <ol className={'children level-' + level}>
                {nodeList}
                </ol>
            );
        }

        return null;
        
    }
}


