import React from 'react';

export default class Node extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            expand: this.props.expand
        };
    }

    render() {
        var node = this.props.data;
        var isRoot = !!this.props.isRoot;

        return (
            <div className={'node' + (isRoot ? ' node_root' : '')} node-id={this.props.id}>
                <div className="node__text">{node.name}</div>
                {this.getToggleBtn(node, this.state.expand)}
            </div>
        );
    }

    getToggleBtn(node, expand) {
        if (node.children && node.children.length) {
            return <span className="toggle-btn">{expand ? '-' : '+'}</span>;
        }

        return '';
    }

    toggleExpand(isExpand) {
        var isExpand = false;

        if (typeof isExpand === 'undefined') {
            isExpand = !this.state.expand;
        } else {
            isExpand = !!isExpand;
        }

        this.setState({
            expand: isExpand
        });

        return this;
    }
}


